import React, { useState } from "react";
import { Grid, Button } from "@material-ui/core";
import { useStyles } from "./styles";

import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";

const imgs = {
  img1: {
    url: "https://aracoiaba.ce.gov.br/fotos/15/Capa15.png",
  },
  img2: {
    url:
      "https://lh3.googleusercontent.com/proxy/3sTQDjWuGHtGDfIyCtdobG8EBNvJ9_vRClZHVgzsgI4wT5jvT3OUzuvl6TEqX3RQ3Q3_VF7DEyslw8rSsPfjRoRMS9OBOsM6IX1GWKSzaGPNzMBGJEW5hZwzylmObV33oYEsLJrgXTfSJ7Fgn5pWBtrX5d9ujx0Z",
  },
  img3: {
    url:
      "https://s2.glbimg.com/w3X6_NIzcGSumH-5nPlPjab51uM=/0x0:1600x900/984x0/smart/filters:strip_icc()/i.s3.glbimg.com/v1/AUTH_59edd422c0c84a879bd37670ae4f538a/internal_photos/bs/2020/B/6/WotJdtQxmuWtwUWIRUIg/avanco-coronavirus-pb.jpg",
  },
};

var sizeObject = Object.keys(imgs).length;
var width = window.screen.availWidth;
var height = window.screen.availHeight;

export const Begin = () => {
  const [image, setImage] = useState(1);
  const classes = useStyles();

  const backImage = () => {
    if (image === 1) {
      setImage(sizeObject);
    } else {
      setImage(image - 1);
    }
  };

  const nextImage = () => {
    if (image === sizeObject) {
      setImage(1);
    } else {
      setImage(image + 1);
    }
  };

  return (
    <div className={classes.root}>
      <Grid container direction="column" justify="center">
        <Grid item>
          <img
            width={width / 1.5}
            height={width * 0.4}
            src={imgs["img" + image].url}
          ></img>
        </Grid>
        <Grid item>
          <Grid container direction="row" justify="center">
            <Grid item>
              <Button onClick={backImage}>
                <ArrowBackIcon />
              </Button>
            </Grid>

            <Grid item>
              <Button onClick={nextImage}>
                <ArrowForwardIcon />
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
};
