import React from "react";
import { iframeStyles } from "./iframeStyle";

export default function Iframe(props) {
  const classes = iframeStyles();
  return (
    <div>
      <iframe
        title={props.title}
        className={classes.iframe}
        width={props.width}
        height={props.height}
        src={props.src}
        frameborder="0"
        allowFullScreen="true"
      ></iframe>
    </div>
  );
}
